@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form-main {
    width: 40%;
    margin-left: 30%;
    margin-top: 3%;
    padding: 25px;
    background: #7a92c5;
    border-radius: 10px;
    font-family: 'Roboto', sans-serif;
    box-shadow: 6px 3px #94AFC3;
}
.Title {
    text-align: center;
    margin-top: 10%;
    font-size: 30px;
    font-weight: 400;
    color: #6b5e5e;
}

.resume {
    text-align: center;
    float: right;
    margin-top: 11px;
}
button.btn.btn-primary.button.clear-button {
    float: right;
}
button.btn.btn-primary.button {
    padding: 4px;
    font-size: 15px;
    background: #6076a5;
     border: none;
}

button.btn.btn-primary.button:hover {
    padding: 4px;
    font-size: 15px;
    background: #6a82b4;
     border: 2px; 
}

.form-main label {
    font-size: 15px;
    color: #46324c;
}

.resume a:hover {
    color: #0056b3;
    text-decoration: none;
}
.resume a {
    color: #282e97;
    text-decoration: none;
}

p#mainh1 {
    font-size: 14px;
}

.main-background {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: #dadfea;
}
